import gql from "graphql-tag";
import * as React from "react";
import { Query } from "react-apollo";

const lifestyleQuery = gql`
  {
    lifestyle {
      imageUrl: imageUrl
      uuid: uuid
    }
  }
`;

export class Lifestyle extends React.PureComponent {
  public render() {
    return (
      <Query query={lifestyleQuery}>
        {({ loading, error, data }) => {
          if (loading) {
            return <p>Loading...</p>;
          }
          if (error) {
            return <p>Error :(</p>;
          }

          const images = data.lifestyle.map(({ imageUrl, uuid }: any) => {
            return <img key={uuid} src={imageUrl} />;
          });

          const numberOfIMages = images.length;
          console.log(numberOfIMages);
          const randomImageIndex = Math.floor(Math.random() * numberOfIMages);
          console.log(randomImageIndex);
          return images[randomImageIndex];
        }}
      </Query>
    );
  }
}



